.row {
  display: flex;
  justify-content: center;
  align-items: center;
}
.but {
  width: 50px;
  height: 50px;
  margin: 4px;
}
.but::hover {
  background-color: green;
}

.slider {
  appearance: none;
  border-radius: 20px;
  width: 100px;
  height: 25px;
  background-color: #d3d3d3;
  outline: none;
  opacity: 0.7;
  transition: opacity 0.2s;
}

.slider:hover {
  opacity: 1;
}

.app {
  display: flex;
  width: 100%;
  height: 100vh;
  justify-content: center;
  align-items: center;
}

.controls {
  width: 20%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.maze {
  width: 70%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  overflow: scroll;
}

.movement {
  width: 10%;
}
